@import url('https://fonts.googleapis.com/css2?family=Poiret+One&family=Sacramento&display=swap');

body {
  padding: 0;
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}
h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

.light-bg {
  /* background-image: 
  url("./image.png");
    width: 100%;
    height: 830px;
    background-repeat: no-repeat;
    background-size: cover; */
    background: linear-gradient(220deg,#3ed4ee,#1171ef)!important;
    width: 100%;
    height: 770px;
    clip-path: polygon(0% 20%,
                  0% 0%,
                  100% 0%,
                 100% 85%,
                  0% 100%,
                  0% 90%,
                   0% 20%);
}
.typewr{
  font-family: 'Poiret One', cursive;
  color:#e2ffbc;
  font-weight:bolder ;
}

.white {
  color: #FFFFFF;
}
.gray {
  color: rgb(65, 65, 65);
  font-size: 30px;
}
.green {
  color: #4dba87;
}
.flex {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.flex-full {
  flex: 1;
}
.justify-space {
  justify-content: space-between;
}
section {
  padding: 5rem 5rem;
}
.button{
  border: none;
  border-radius: 0.2rem;
  padding: 0.5rem 1rem;
  color: #FFFFFF;
  font-size: 0.8rem;
  font-weight: 600;
  background-color: #4dba87;
  cursor: pointer;
}

/* Header */
nav {
  padding: 1.1rem 5rem;
}
nav .logo {
  font-size: 1.5rem;
  font-weight: 600;
}
nav ul li {
  list-style-type: none;
  margin-left: 3rem;
  font-size: 0.9rem;
  font-weight: 600;
}
.header {
  justify-content: space-between;
  padding: 1.1rem 5rem 0rem 5rem;
}
.header h1 {
  margin: auto;
  font-size: 3rem;
  font-weight: 500;
}
.header p {
  font-size: 0.8rem;
  font-weight: 500;
  margin-top: 1rem;
  margin-bottom: 2rem;
}
.header button {
  cursor: text;
  margin-bottom: 1.5rem;
}
.header a {
  font-size: 1.2rem;
  font-weight: 600;
  text-transform: uppercase;
  text-decoration: underline;
  cursor: pointer;
}

/* About */
.about-card {
  width: 90%;
  padding: 2rem;
  margin-top: 0.5em;
  border-radius: 0.5rem;
}
.about-card h3 {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 0.3rem;
}
.about-card p {
  font-size: 0.8rem;
  font-weight: 500;
  margin-bottom: 1rem;
}
.about-card span {
  font-size: 0.8rem;
  font-weight: 600;
  text-transform: uppercase;
  text-decoration: underline;
}
.about-text h1 {
  margin-top: 1rem;
  margin-bottom: 2rem;
  font-weight: 500;
  font-size: 2rem;
}
.about-text h3 {
  margin-bottom: 1rem;
  font-weight: 500;
  font-size: 1.5rem;
  font-style: italic;
  text-align: justify;
}
.about-text p {
  font-weight: 500;
  font-size: 1rem;
  text-align: justify;
}
.partners {
  padding-top: 5rem;
}

/* Footer */
footer {
  padding: 2rem 5rem;
}
footer span {
  font-size: 1rem;
  font-weight: 500;
  text-transform: uppercase;
}
footer ul li {
  list-style-type: none;
  margin-left: 1rem;
}

.skills{
 
  width: 50%;
  height: auto;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(4,1fr);
  gap: 20px;
  margin: auto;
}
.skillcard{
  
  width: 200px;
  height: 270px;
}
.skillcard>img{
  width: 160px;
  height: 210px;
  margin: auto;
}
.skillcard>h3{
  margin-bottom: 10px;
  
  text-align: center;
}
.sammyakimg{
  margin-top: -20px;
  width: 300px;
}

/* --------- Responsive Design --------- */
@media (max-width: 768px) {
  .light-bg {
    background: linear-gradient(220deg,#6edbee,#488ce5)!important;
    width: 100%;
    height: 830px;
    clip-path: polygon(0% 20%,
                  0% 0%,
                  100% 0%,
                 100% 92%,
                  0% 100%,
                  0% 90%,
                   0% 20%);
}
  .flex {
    row-gap: 2rem;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .white{
    font-size: medium;
  }
  .fontsty{
    font-size: 20px;
  }
  /* Header */
  nav {
    padding: 1rem 2rem;
    flex-direction: row !important;
    justify-content: space-between !important;
  }
  nav ul, nav ul li, .active {
    display: none;
  }
  .header {
    padding: 2rem 2rem 0rem 2rem;
  }
  section {
    padding: 1rem 2rem;
  }
  /* About */
  .about-card {
    width: 100%;
  }
  .about-card div {
    flex-direction: row !important;
    justify-content: space-between !important;
  }
  /* Footer */
  footer ul {
    column-gap: 1rem;
    flex-direction: row !important;
  }
  footer ul li {
    margin-left: 0;
  }
  .sammyakimg{
    width: 280px;
  }
}
@media only screen and (max-width: 426px){
  .light-bg {
      background: linear-gradient(220deg,#11cdef,#1171ef)!important;
      width: 100%;
      height: 630px;
      clip-path: polygon(0% 20%,
                    0% 0%,
                    100% 0%,
                   100% 100%,
                    0% 100%,
                    0% 90%,
                     0% 20%);
  }
  .header{
    width: 100%;
    height: auto;
  }
  .content{
   margin-top: 20px;
  }
  .fontsty{
    font-size: 20px;
  }
   h1{
    font-size: 10px;
  }
 
 .sammyakimg{
  width: 190px;
 
 
 }
 .typewr{
  font-size: 25px;
 }


}
